@import url(https://fonts.googleapis.com/css2?family=Gorditas&display=swap);
.layout .logo {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.layout .logo img {
    width: 200px;

    padding-top: 50px;
    padding-bottom: 5px;
}

